.subscription__container {
  display: flex;
  flex-direction: column;
  gap: 38px;
  align-items: center;
  position: absolute;
  z-index: 1;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 50%;
}

.subscription__title {
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
