}
.subscription__button--current::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: #D10A42;
  position: absolute;
  top: 38px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.subscription__wrapper {
  position: absolute;
  right: 0;
  bottom: -180px;
