  font-size: 12px;
  font-weight: 700;
  line-height: 12px;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  border: 2px solid #ffffff;
  max-height: 66px;
  padding: 25px 54px;
}

.games__title {
  color: #102654;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin-bottom: 45px;
}

.poster {
  transition: opacity 350ms ease-in;
}

.poster__wrapper {
  position: absolute;
  top: 0;
}
.poster__wrapper::after {
  content: "";
  width: 100%;
  min-height: 228px;
  background: rgba(20, 54, 122, 0.5);
  display: block;
  position: absolute;
  top: 0;
}
.poster__wrapper--del {
  display: none;
}
.poster__wrapper--hidden {
  opacity: 0;
}

.poster__button {
  border: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  min-height: 100px;
  min-width: 100px;
}

.poster__text {
  border-radius: 50px;
  background: linear-gradient(97deg, #ED0233 17.6%, #003EB7 95.31%);
  width: 80px;
  height: 80px;
  display: block;
  position: absolute;
  top: 10px;
  left: 10px;
}

.poster__text svg {
  position: absolute;
  top: -10px;
  left: -10px;
}

